.menu-btn {
  position: absolute;
  z-index: 3;
  right: 35px;
  top: 35px;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.menu-btn .btn-line {
  width: 28px;
  height: 3px;
  margin: 0 0 5px 0;
  background: #F0F0F0;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.menu-btn.close {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.menu-btn.close .btn-line:nth-child(1) {
  -webkit-transform: rotate(45deg) translate(5px, 5px);
          transform: rotate(45deg) translate(5px, 5px);
}

.menu-btn.close .btn-line:nth-child(2) {
  opacity: 0;
}

.menu-btn.close .btn-line:nth-child(3) {
  -webkit-transform: rotate(-45deg) translate(7px, -6px);
          transform: rotate(-45deg) translate(7px, -6px);
}

.menu {
  position: fixed;
  top: 0;
  width: 100%;
  visibility: hidden;
}

.menu.show {
  visibility: visible;
}

.menu-brand,
.menu-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  float: left;
  width: 50%;
  height: 100vh;
  overflow: hidden;
}

.menu-nav {
  margin: 0;
  padding: 0;
  background: rgba(42, 42, 42, 0.95);
  list-style: none;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.menu-nav.show {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.menu-brand {
  background: rgba(55, 55, 55, 0.95);
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.menu-brand.show {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.menu-brand img {
  max-width: 250px;
  border: 3px solid #6ABEDB;
  border-radius: 50%;
}

.menu .nav-item {
  -webkit-transform: translate3d(600px, 0, 0);
          transform: translate3d(600px, 0, 0);
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.menu .nav-item.show {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.menu .nav-item.current>a {
  color: #6ABEDB;
}

.menu .nav-link {
  display: inline-block;
  position: relative;
  font-size: 30px;
  text-transform: uppercase;
  padding: 1rem 0;
  font-weight: 300;
  color: #F0F0F0;
  text-decoration: none;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.menu .nav-link:hover {
  color: #6ABEDB;
}

.nav-item:nth-child(1) {
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}

.nav-item:nth-child(2) {
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
}

.nav-item:nth-child(3) {
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
}

.nav-item:nth-child(4) {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}

* {
  box-sizing: border-box;
}

body {
  background: #373737;
  color: #F0F0F0;
  min-height: 100%;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
}

body.bg-img {
  background: url(/static/media/background.40451a41.jpg);
  background-attachment: fixed;
  background-size: cover;
}

body.bg-img:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: rgba(55, 55, 55, 0.85);
}

small {
  font-size: 0.6rem;
  margin-bottom: 0.5rem;
}

.container {
  max-width: 1080px;
  margin: 0 auto;
}

h1,
h2,
h3 {
  margin: 0;
  font-weight: 400;
}

h1.lg-heading,
h2.lg-heading,
h3.lg-heading {
  font-size: 6rem;
}

h1.sm-heading,
h2.sm-heading,
h3.sm-heading {
  margin-bottom: 2rem;
  padding: 0.2rem 1rem;
  background: rgba(60, 60, 60, 0.5);
}

a {
  color: #fff;
  text-decoration: none;
}

header {
  position: fixed;
  z-index: 2;
  width: 100%;
}

.text-secondary {
  color: #6ABEDB;
}

.text-warning {
  color: #FEDD56;
}

main {
  padding: 4rem;
  min-height: calc(100vh - 60px);
}

main .icons {
  margin-top: 1rem;
}

main .icons a {
  padding: 0.4rem;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

main .icons a:hover {
  color: #6ABEDB;
}

main#home {
  overflow: hidden;
}

main#home h1 {
  margin-top: 20vh;
}

.about-info {
  display: grid;
  grid-gap: 30px;
  grid-template-areas: 'bioimage bio bio''bioresume bio bio''. bio bio';
  grid-template-columns: repeat(3, 1fr);
}

.about-info .bio-image {
  grid-area: bioimage;
  margin: auto;
  border: #6ABEDB 3px solid;
}

.about-info .bio-resume {
  grid-area: bioresume;
  margin: auto;
  border: none;
}

.about-info .bio {
  grid-area: bio;
  font-size: 1.3rem;
}

@-webkit-keyframes shake {

  10%,
  90% {
    -webkit-transform: translate3d(-1px, 1px, 0);
            transform: translate3d(-1px, 1px, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 2px);
            transform: translate3d(2px, 0, 2px);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, -4px, 0);
            transform: translate3d(-4px, -4px, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 4px);
            transform: translate3d(4px, 0, 4px);
  }
}

@keyframes shake {

  10%,
  90% {
    -webkit-transform: translate3d(-1px, 1px, 0);
            transform: translate3d(-1px, 1px, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 2px);
            transform: translate3d(2px, 0, 2px);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, -4px, 0);
            transform: translate3d(-4px, -4px, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 4px);
            transform: translate3d(4px, 0, 4px);
  }
}

.skills {
  font-size: 1.3rem;
}

.skills .skill-grid {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  background: #F0F0F0;
  color: #201F1D;
  margin: 1em;
  padding: 0.75em;
  border: 3px solid #6ABEDB;
  border-radius: 5%;
}

.skills .skill-grid .skill-item {
  text-align: center;
  -webkit-align-self: center;
          align-self: center;
  -webkit-align-items: center;
          align-items: center;
}

.skills .skill-grid .skill-item .skill-img {
  max-width: '100px';
  height: 'auto';
}

.skills .skill-grid .skill-item .skill-img:hover {
  -webkit-animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
          animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.timeline ul li {
  list-style-type: none;
  position: relative;
  width: 6px;
  margin: 0 auto;
  padding-top: 50px;
  background: #F0F0F0;
}

.timeline ul li::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: inherit;
}

.timeline ul li div {
  position: relative;
  bottom: 0;
  width: 400px;
  padding: 15px;
  background: #F0F0F0;
  color: #201F1D;
  border: 3px solid #6ABEDB;
}

.timeline ul li div h3,
.timeline ul li div h4 {
  color: #201F1D;
  margin: 0;
  font-size: 1.2em;
  font-weight: bold;
}

.timeline ul li div::before {
  content: '';
  position: absolute;
  bottom: 7px;
  width: 0;
  height: 0;
  border-style: solid;
}

/* Odd elements */
.timeline ul li:nth-child(odd) div {
  left: 45px;
}

.timeline ul li:nth-child(odd) div::before {
  left: -15px;
  border-width: 8px 16px 8px 0;
  border-color: transparent #F0F0F0 transparent transparent;
}

/* Even elements */
.timeline ul li:nth-child(even) div {
  left: -439px;
}

.timeline ul li:nth-child(even) div::before {
  right: -15px;
  border-width: 8px 0 8px 16px;
  border-color: transparent transparent transparent #F0F0F0;
}

time {
  display: block;
  font-size: 1.3rem;
  font-weight: bold;
  margin-bottom: 5px;
}

.project-grid {
  display: grid;
  grid-gap: 0.7rem;
  grid-template-columns: repeat(3, 1fr);
}

.project-grid img {
  width: 100%;
  border: 3px #fff solid;
}

.project-grid img:hover {
  opacity: 0.5;
  border-color: #6ABEDB;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  cursor: default;
}

.boxes {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 1rem;
}

.boxes div {
  font-size: 2rem;
  border: 3px #fff solid;
  padding: 1.5rem 2.5rem;
  margin-bottom: 3rem;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.boxes div:hover {
  padding: 0.5rem 1.5rem;
  background: #6ABEDB;
  color: #201F1D;
}

.boxes div:hover span {
  color: #201F1D;
}

.btn,
.btn-dark,
.btn-light,
.btn-error {
  display: block;
  padding: 0.5rem 1rem;
  border: 0;
  margin-bottom: 0.3rem;
}

.btn:hover,
.btn-dark:hover,
.btn-light:hover,
.btn-error:hover {
  background: #6ABEDB;
  color: #201F1D;
}

.btn-dark {
  background: black;
  color: #F0F0F0;
}

.btn-light {
  background: #b7b7b7;
  color: #201F1D;
}

.btn-error {
  background: #b60101;
  color: #F0F0F0;
}

#footer {
  padding: 1rem;
  background: #1e1e1e;
  color: #F0F0F0;
  height: 150px;
}

#footer .footer-grid {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  -webkit-align-content: start;
          align-content: start;
  -webkit-justify-content: center;
          justify-content: center;
  grid-template-areas: 'info address contact';
}

#footer .footer-grid .footer-info {
  grid-area: info;
}

#footer .footer-grid .footer-address {
  grid-area: address;
}

#footer .footer-grid .footer-contact {
  grid-area: contact;
}

#footer .footer-grid ul li {
  list-style-type: none;
}

@media screen and (min-width: 1171px) {
  .project-grid {
    grid-template-columns: repeat(4, 1fr);
  }

  .skills .skill-grid {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media screen and (min-width: 769px) and (max-width: 1170px) {
  .project-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .skills .skill-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (max-width: 768px) {
