    -webkit-align-items: center;
            align-items: center;
    text-align: center;
  }

  main .lg-heading {
    line-height: 1;
    margin-bottom: 1rem;
  }

  ul.menu-nav,
  div.menu-brand {
    float: none;
    width: 100%;
    min-height: 0;
  }

  ul.menu-nav.show,
  div.menu-brand.show {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  .menu-nav {
    height: 70vh;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    font-size: 24px;
  }

  .menu-brand {
    height: 30vh;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }

  .menu-brand img {
    max-width: 150px;
  }

  .about-info {
    grid-template-areas: 'bioimage''bioresume''bio''.';
    grid-template-columns: 1fr;
  }

  .timeline ul li {
    margin-left: 10px;
  }

  .timeline ul li div {
    width: 350px;
  }

  .timeline ul li:nth-child(even) div {
    left: 45px;
  }

  .timeline ul li:nth-child(even) div::before {
    left: -15px;
    border-width: 8px 16px 8px 0;
    border-color: transparent #6ABEDB transparent transparent;
  }

  .project-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .skills {
    font-size: 1.1rem;
  }

  .skills .skill-grid {
    grid-gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    border-radius: 0;
  }

  .skills .skill-grid .skill-item .skill-img {
    max-width: '85px';
  }

  #footer {
    padding: 0.75rem;
    height: 300px;
    text-align: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: start;
            justify-content: start;
  }

  #footer .footer-grid {
    grid-template-columns: 1fr;
    grid-template-areas: 'info''address''contact';
  }
}

@media screen and (max-width: 500px) {
  main {
    padding: 2rem;
  }

  main #home h1 {
    margin-top: 10vh;
  }

  main .lg-heading {
    margin-top: 1rem;
    font-size: 5rem;
  }

  h1,
  h2,
  h3 {
    margin: 0;
    font-weight: 350;
  }

  h1.lg-heading,
  h2.lg-heading,
